.blog_grid {
    display: grid;
    grid-template-columns: 2fr 1fr;
    grid-template-rows: 1fr;
    grid-column-gap: 48px;
}

.blog_posts {
    display: flex;
    flex-direction: column;
    gap: 30px;
}

.post {

    a {
        color: inherit;
        text-decoration: none;
    }
    .post_thumbnail {
        margin-bottom: 30px;
    }

    .post_meta {
        margin-bottom: 7px;
        color: #a3a199;

        .post_category {
            a {
                color: #a3a199;
            }
        }
    }

    .post_title {
        font-weight: 600;
        font-size: 28px;
        line-height: 32px;
        letter-spacing: 0.01em;
        color: #171822;
        margin-bottom: 11px;
    }

    .excerpt {
        p {
            font-weight: 400;
            font-size: 16px;
            line-height: 26px;
            letter-spacing: 0.01em;
            color: #171822;
        }
    }
    .large_post {
       
    }
}